<template>
    <div>
        <div class="padding">
            <ul class="list">
                <li v-for="(item, index) in list" :key="index" @click="toggleSelect(item)">
                    <span>{{item.name}}</span>
                    <img
                        src="../../assets/selected.png"
                        v-if="item.selected == 1">
                    <img
                        src="../../assets/unselected.png"
                        v-else>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    props: ['list'],
    data () {
        return {

        };
    },
    methods: {
        toggleSelect (item) {
            item.selected = item.selected === 1 ? 0 : 1;
        }
    }
};
</script>

