// 行业类别
<template>
    <div>
        <type-list :list='listData'></type-list>
        <confirm-btn @confirmClick="confirmClick"></confirm-btn>
    </div>
</template>

<script>
import TypeList from '../../components/personal/type-list';
import ConfirmBtn from '../../components/common/confirm-button';
import { mapMutations, mapState } from 'vuex';
import { publicGet } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
export default {
    data () {
        return {
            listData: [] // 列表数据
        };
    },
    computed: {
        ...mapState({
            industryTypes: state => state.personal.industryTypes
        })
    },
    methods: {
        ...mapMutations({
            setIndustry: 'setIndustry'
        }),
        // 确认
        confirmClick () {
            let arr = [];
            this.listData.forEach(ele => {
                if (ele.selected === 1) {
                    arr.push(ele);
                }
            });
            // 数据存在store
            this.setIndustry(arr);
            this.$router.go(-1);
        }
    },
    created () {
        this.$router.setTitle(this.$t('title.editIndustry'));
        let obj = {
            userId: this.$route.query.userId
        };
        publicGet(getApiUrl('getIndustryTypes'), obj).then((res) => {
            if (this.industryTypes && this.industryTypes.length > 0) {
                res.forEach(ele => {
                    ele.selected = 0;
                });
                this.industryTypes.forEach(ele => {
                    res.forEach(item => {
                        if (item.name === ele.name) {
                            item.selected = 1;
                        }
                    });
                });
            }
            this.listData = res;
        }).catch(() => {
            console.warn('error');
        });
    },
    components: {
        TypeList,
        ConfirmBtn
    }
};
</script>

<style scoped lang='scss'>
    @import '../../scss/variables.scss';
</style>
